#loader {
  border: 16px solid #00f;
  border-left: 16px #f3f3f3;
  border-right: 16px #f3f3f3;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: 2s linear infinite spin;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

body {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  display: flex;
}

@media (width <= 600px) {
  body {
    flex-direction: column;
  }
}
